<template>
  <div>
    <button @click="one">首页</button>
    <button @click="up">上一页</button>
    <button v-for="(btn,index) in pagebtns" :key="index" 
        :class="[{currentPage:btn == currentPage},'pagebtn']" @click="changeBtn(btn)">{{btn}}</button>
    <button @click="down">下一页</button>    
  </div>    
</template>

<script>
export default {
    data(){
        return{
            pagebtns:[1,2,3,4,5,'......'],
            currentPage:1
        }
    },
    methods:{
        changeBtn(a){
            this.currentPage = a
            if(a == this.pagebtns[4]){
                this.pagebtns.shift()
                this.pagebtns.splice(4,0,this.pagebtns[3]+1)
            }else if(a == this.pagebtns[0] && a != 1){
                this.pagebtns.splice(4,1)
                this.pagebtns.unshift(this.pagebtns[0]-1)
            }
            this.$emit('handel',this.currentPage)
        },
        one(){
            this.pagebtns = [1,2,3,4,5,'......']
            this.currentPage = 1
            this.$emit('handel',this.currentPage)
        },
        up(){
            if(this.currentPage != 1 ){
            this.pagebtns.splice(4,1)
            this.pagebtns.unshift(this.pagebtns[0]-1)
            this.currentPage = this.currentPage -1
            this.$emit('handel',this.currentPage)
            }
        },
        down(){
            this.pagebtns.shift()
            this.pagebtns.splice(4,0,this.pagebtns[3]+1)
            this.currentPage = this.currentPage +1
            this.$emit('handel',this.currentPage)
        }
    }
}
</script>

<style scoped>
  .pagination {
    margin-top: 5px;
    margin-bottom: 20px;
    background-color: white;
    padding: 6px 20px;
    border-radius: 5px;
    /*box-shadow: 0px 2px 9px #888888;*/
    border: 1px solid #888888;
  }

  button {
    background-color: #fff;
    border: 1px solid #ddd;
    color: #778087;
    border-radius: 3px;
    outline: none;
    height: 21px;
    cursor: pointer;
    padding: 0 2px;
    width: 55px;
    height: 29px;
  }

  .pagebtn {
    position: relative;
    bottom: 1px;
    width: 40px;
    margin: 0 4px;
  }

  .currentPage {
    color: white;
    background-color: #1f1b1b;

  }
</style>